class SingleUserBook extends Component {
constructor(props){
    super()
    this.loadModules = this.loadModules.bind(this)
this.state={enabled_items:[],edit: false}
this.renderAgain = this.renderAgain.bind(this);
this.setAmountItems = this.setAmountItems.bind(this);
}



renderAgain(){
    this.loadModules()
    this.render()
}



    componentDidMount(){
      this.loadModules()
    }

    async setAmountItems(i){
      
    }


async loadModules(){
    let total_modules=[]
    let rendAgain=() => this.renderAgain
    let showBoxx=this.showBox
    console.log("ENTER");
    let edit=this.state.edit
    let setamountnull=() => this.setAmountItems(0)
    await window.ethereum.enable().then(async function (accounts) {   
        const ownsership = new web3.eth.Contract(OWNERSHIPABI, OWNERSHIP) 
        console.log(await ownsership.methods.owned(accounts[0],0).call());   
        let owned=await ownsership.methods.owned(accounts[0],0).call()
        console.log("COUNT"+owned[1]);
        for(let i=0;i<owned[1];i++){
            let addr
            addr=await ownsership.methods.owned(accounts[0],i).call()
            console.log("-----------------------------------------------")
            console.log("MODULE ADDRESS: "+addr[0])
            const happybox = new web3.eth.Contract(BOXABI, addr[0]) 
            let count=0;
            for(let j=0;j<count+1;j++){
                let addr
                let found=false
                try{
                addr=await happybox.methods.modules_list(j).call()
                
                    found=true
                    count++
                }catch(e){}  
                if(found){ 
                    let code=0  
                    try{
                    let module = new web3.eth.Contract(TICKETABI, addr)
                    let codex=await module.methods.code().call()
                    code=codex
                    }catch(f){} 
                    let enabled=await happybox.methods.modules(addr).call()
                    if(enabled){
                      if(code==1){
                         
                          document.getElementById("UserBook_access_point_count").innerHTML=' '+i+'   '+total_modules.length
                          console.log("TICKET ADDRESS: "+addr)
                          total_modules.push(<PRiceList1_0 edit={true} address={addr} key={total_modules.length} index={total_modules.length} setAmount={() => setamountnull()}></PRiceList1_0>)
                            
                          //total_modules.push(<Ticket1_0 address={addr} key={total_modules.length} index={total_modules.length} enabled={true} box={addr[0]} reset={rendAgain} ticket={addr}></Ticket1_0>)
                      }
                    }      
                }  
            }
            //t=<Box1_0 address={addr[0]} key={i} index={i} enabled={true} reset={rendAgain} showBox={showBoxx}></Box1_0>
            //total_modules.push(addr[0])               
        }     
    })
    this.setState({enabled_items : total_modules})
    this.render()
}

    render(props){
    console.log(this.state.enabled_items)

    
        return ( <>
        <div id="UserBook_access_point_count" className="UserBook_mainlabel left"> 0   0 access points</div> 
            <div className="UserBook_mainlabel right"> user book  </div> 
        

<div className="clear"></div> 
<div className="UserBook_Container"> 
                {
                  this.state.enabled_items  
                }      
      </div>
            </>
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

