import React, { Component } from 'react'
import Web3 from 'web3'

import './css.css';

class InstaClaimer extends Component {

    constructor(props){
        super()
    }

    componentDidMount(){
        this.loadData()
    }

    async claim(){
        await window.ethereum.enable().then(async function (accounts) {      
            const lockdrop = new web3.eth.Contract(INSTACLAIMERABI, INSTACLAIMER) 
            await lockdrop.methods.instaClaim().send({ gasLimit: "101293" , from: accounts[0]})       
        })
        this.loadData()
    }

    async loadData(){
        window.ethereum.enable().then(async function (accounts) {      
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            let total=await beans.methods.balanceOf(INSTACLAIMER).call()
            document.getElementById("InstaClaimer_reward_total").innerHTML=" "+(total/1000000000000000000)+" Beans <center>period  claim once</center>"

    })
    }

    render(){
      
       
        return ( 

        <div className="InstaClaimer_module">
            <div><span className="pointer" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/address/0x09F7F17c47A66E19e3A6ce4d3D6E669E5709De0E")}}></span> InstaClaimer</div>
            <div className="InstaClaimer_panel">
            <div className="InstaClaimer_board">
            <div className="InstaClaimer_reward_token" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/token/0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD")}}></div>
            <div className="InstaClaimer_reward_description" id="InstaClaimer_reward_description" aria-labelledby="jsx-a11y/accessible-emoji" role="img">Claim Beans from a stake!</div>
            <div className="InstaClaimer_reward_available" aria-labelledby="jsx-a11y/accessible-emoji" role="img">available:</div>
            <div className="InstaClaimer_reward_total pointer" id="InstaClaimer_reward_total" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/tokenholdings?a=0xdFCe2f79f8c3Bf1765D9563a6b1BDAfb117E72BF")}}> 30 Beans     
            <center>period  claim once</center>
            </div>  
            <input  id="InstaClaimer_stake_button" className="InstaClaimer_stake_button" type="button" onClick={() => this.claim()} value=" Claim now!"/> 
            

            </div>
            </div>
        </div>
            
        );

    

   
    }


}

    let INSTACLAIMERABI=[ { "inputs": [], "stateMutability": "nonpayable", "type": "constructor" }, { "inputs": [], "name": "instaClaim", "outputs": [ { "internalType": "bool", "name": "", "type": "bool" } ], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [], "name": "staker", "outputs": [ { "internalType": "contract PeriodicStaker", "name": "", "type": "address" } ], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "tot", "outputs": [ { "internalType": "uint256", "name": "", "type": "uint256" } ], "stateMutability": "view", "type": "function" } ]
