import React, { Component } from 'react'
import Web3 from 'web3'

import './css.css';


let value="xxx";
class MiniStaker extends Component {

    constructor(props){
        super()
        this.state={value:"xxx"}
    }
        change(txt){
            console.log(txt)
            this.setState={value:txt}
            this.render()
        }

    componentDidMount(){
        this.loadData()
    }

    async approve(){
        window.ethereum.enable().then(async function (accounts) {  
            
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            let total=await beans.methods.allowance(accounts[0],PERIODICSTAKER).call()
            let isApproved=(total>0)?true:false 

            if(!isApproved){
            const beans = new web3.eth.Contract(tokenABI, BEANS) 

            await beans.methods.approve(PERIODICSTAKER,web3.utils.toBN(web3.utils.toWei("10000"))).send({gasPrice: '1234567', from: accounts[0]})
            document.getElementById("MiniStaker_stake_button").value=" stake!"
            }else{
                const staker = new web3.eth.Contract(PERIODISTAKERABI, PERIODICSTAKER) 
                if(document.getElementById("MiniStaker_stake_amount").value==""){
                    alert("Stake an amount of BEANS > 0")
                }else{
                
                document.getElementById("MiniStaker_stake_button").value="staking.."
                await staker.methods.stakeNow(web3.utils.toBN(web3.utils.toWei(document.getElementById("MiniStaker_stake_amount").value))).send({gas: '230000', gasPrice: '174747', from: accounts[0]})
                //await staker.methods.stakeNow("0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD",1000000).send({gas: '230000', gasPrice: '174747', from: accounts[0]})
               
                //this.loadData()
                document.getElementById("MiniStaker_stake_button").value=" stake!"
            }
            }
    })   
        
    }

    unstake(){
        window.ethereum.enable().then(async function (accounts) {  
            
            const staker = new web3.eth.Contract(PERIODISTAKERABI, PERIODICSTAKER) 
            let total=await staker.methods.unstake().send({gasPrice: '12345678', from: accounts[0]})

            })

    }



    async loadData(){
        window.ethereum.enable().then(async function (accounts) {   
            
            const beans = new web3.eth.Contract(tokenABI, BEANS) 
            const lockdrop = new web3.eth.Contract(LOCKDROPABI, LOCKDROPBEANS) 
            let allowance=await beans.methods.allowance(accounts[0],PERIODICSTAKER).call()
            let isApproved=(allowance>0)?true:false 
            if(isApproved)document.getElementById("MiniStaker_stake_button").value=" stake!"
            if(!isApproved)document.getElementById("MiniStaker_stake_button").value=" approve"
   
    })
    }

    render(){
      
       
        return ( 
        <div className="LockDrop_module">
            <div><span className="pointer" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/address/0xdFCe2f79f8c3Bf1765D9563a6b1BDAfb117E72BF")}}></span> MiniStaker</div>
            <div className="LockDrop_panel">
            <div className="MiniStaker_board">
            <div className="LockDrop_reward_token" aria-labelledby="jsx-a11y/accessible-emoji" role="img" onClick={() => {window.open("https://etherscan.io/token/0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD")}}></div>
            <div className="LockDrop_reward_description" id="LockDrop_reward_description" aria-labelledby="jsx-a11y/accessible-emoji" role="img">stake / unstake</div>
            <div className="LockDrop_reward_available" aria-labelledby="jsx-a11y/accessible-emoji" role="img">stake an amount or unstake all:</div>
            <div className="LockDrop_reward_total pointer" id="LockDrop_reward_total" aria-labelledby="jsx-a11y/accessible-emoji" role="img">&nbsp;</div>
             
            <div className="LockDrop_reward_amount" aria-labelledby="jsx-a11y/accessible-emoji" role="img">amount:</div>
            <input style={{ height: 40, borderColor: 'gray', borderWidth: 1 }} className="LockDrop_stake_amount" id="MiniStaker_stake_amount" type="text"/> 
            <div className="LockDrop_stake_icon" aria-labelledby="jsx-a11y/accessible-emoji" role="img"  onClick={() => {window.open("https://etherscan.io/token/0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD")}}></div>
            
            <div className="clear"/>
            <input  id="MiniStaker_stake_button" className="MiniStaker_stake_button" type="button" value=" approve" onClick={() => this.approve()}/> 
            <input  id="MiniStaker_unstake_button" className="MiniStaker_stake_button" type="button" value=" unstake!" onClick={() => this.unstake()}/> 
            

            </div>
            </div>
        </div>
            
        );

    

   
    }


}

